<template>
  <div class="abcd">
    <br /><br /><br /><br />
    <h2 style="text-align: center; font-weight: bold">Products</h2>
    <br /><br />
    <div class="container" style="height: 50vh">
        <div v-for="product in products" :key="product.id">
        <b-card img-top>
          <b-card-img :src="getImgUrl(product.path)" img-alt="Card image" img-top style="width:30vh"></b-card-img>
          <b-card-text style="text-align:center">{{ product.title }}</b-card-text>
        </b-card>
        </div>
    </div>
    <br /><br /><br /><br /><br /><br /><br /><br /><br />
  </div>
</template>

<script>
export default {
  name: 'Products',
  props: {
    products: Array
  },
  methods: {
    getImgUrl (pic) {
      return require('../assets/products/' + pic + '.jpg')
    }
  },
  data () {
    return {
      mainProps: {
        center: true,
        fluidGrow: true,
        blank: true,
        blankColor: '#bbb',
        class: 'my-5'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
abcd {
  align-items: center;
}
.container {
  display: flex;
  overflow-x: scroll;
}
::-webkit-resizer {
  line-height: 200px;
}
card-img-top {
  height: 20%;
}
</style>



