<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="secondary" fixed="top">
      <b-navbar-brand href="#">
        <img
          src="./../assets/logo.png"
          alt=""
          width="150px"
          height="40px"
          class="d-inline-block align-middle mr-2"
        />
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-nav-item href="/">Home</b-nav-item>
          <b-nav-item href="/#/About">About</b-nav-item>
          <b-nav-item href="/#/Brands">Brands</b-nav-item>
          <b-nav-item href="/#/Products">Products</b-nav-item>
          <b-nav-item href="/#/Contact">Contact</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
  data () {
    return {
      msg: 'Welcome to Your Vue App by InfinoCube'
    }
  },
  methods: {
    hide () {
      console.log('hiding')
      this.$root.$emit('bv::toggle::collapse', 'nav-collapse')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.navbar-brand {
  color: rgb(2, 2, 2);
  font-size: 2.5em;
  padding-left: 50px;
  font-weight: 600;
  font-family: "Lucida Console", "Courier New", monospace;
}
li a:focus {
  color: #10bd83;
  background-color: #94a1a0;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}
nav .navbar-nav li a {
  color: rgb(243, 237, 237) !important;
  margin-right: 50px;
  font-size: 1.2rem;
  font-weight: 200;
  font-family: "Times New Roman", Times, serif;
}
.navbar {
  padding: 0;
}
html,
body {
  margin: 0px;
}
</style>



